  max-width: 30em;
  opacity: 0.3;
}

.section {
  transform: translateY(-100%) scale(0);
  transition: $transition-default;
  overflow: hidden;
  padding-bottom: 3em;
}

.sec1 {
  display: none;
  transform: translateY(0) scale(1);
  background-color: $light-bg-color;
}

.sec2 {
  display: none;
  transform: translateY(0) scale(1);
  background-color: $light-bg-color;
  .section-info {
    max-width: 900px;
    display: flex;
    flex-direction: column;
    justify-content: center;
