        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        gap: 2em;
        @media screen and (max-width: 950px) {
          transform: translateY(-10%);
        }
        .about-sec1-text-heading {
          text-align: left;
          h3 {
            padding-left: 1rem;
            padding-right: 10px;
            font-size: calc(12px + 5vmin);
            span {
              color: $highlight-color;
            }
          }
        }
        .about-sec1-text-par {
          text-align: left;
          background-color: $color-grey-2;
          padding: 2rem;
          position: relative;
          width: 100%;
          box-shadow: -10px 10px 0px 0px $highlight-color;
          p {
            color: $dark-bg-color;
            font-weight: bolder;
            z-index: 5;
            span {
              color: $highlight-color;
            }
          }
          .about-sec1-text-chatbox {
            position: absolute;
            bottom: -10px;
            right: 89px;
            padding: 10px;
            .background {
              content: "";
              position: absolute;
              height: 0rem;
              width: 0rem;
              z-index: 0;
              top: -70px;
              right: -169px;
              border-top: 80px solid $color-grey-2;
              border-right: 80px solid transparent;
              border-bottom: 80px solid transparent;
              border-left: 80px solid transparent;
              transform: rotate(225deg);
              box-shadow: -6px -6px 0px 5px $highlight-color;
            }
            .backgroundTwo {
              border-top: 80px solid transparent;
              border-left: 80px solid $dark-bg-color;
              box-shadow: none;
              top: -54px;
              right: -186px;
              top: -75px;
              right: -169px;
              transform: rotate(225deg) scale(1.2);
            }
            img {
              height: 6rem;
              z-index: 2;
              position: absolute;
            }
          }
        }
      }
      .about-sec1-img {
        width: 100%;
        text-align-last: left;
        position: relative;
        display: flex;
        flex-direction: column;
        div {
          width: 100%;
          display: flex;
          flex-shrink: 0;
          flex-direction: column;
          img {
            max-height: 500px;
            min-height: 500px;
            flex-shrink: 0;
            width: 100%;
            object-fit: cover;
            z-index: 10;
            box-shadow: 10px 10px 0px 0px $highlight-color;
          }
          .about-section1-mobile-background {
            height: 0rem;
            @media screen and (max-width: 780px) {
              content: "";
              height: 9rem;
              width: 100%;
              z-index: 0;
              box-shadow: 5px 5px 0px 5px $highlight-color;
              margin-top: -3rem;
            }
          }
        }
      }
    }
  }
}

.section-info .divider {
  content: "";
  margin: 5em auto;
  display: flex;
  justify-content: center;
  width: 70%;
  height: 3px;
  background-color: $color-grey-6;
  max-width: 30em;
  opacity: 0.3;
}

.sec3 {
  display: none;
  transform: translateY(0) scale(1);
  background-color: $light-bg-color;
}

.sec4 {
  display: none;
  transform: translateY(0) scale(1);
  background-color: $light-bg-color;
  .section-info {
    padding-top: 6em;
    .approach-steps {
      display: flex;
      width: 100%;
      justify-content: center;
      padding-top: 5em;
      i {
        align-self: center;
        font-size: xxx-large;
        color: $highlight-color;
      }
    }
    .approach-step {
      padding: 0.2em 2em;
      margin: 0em 1em;
      h1 {
        margin-bottom: 0.2em;
        color: $highlight-color;
      }
    }
    .approach-descriptions {
      max-width: 50em;
      margin: 0 auto;
      margin-bottom: 5em;
    }
    .approach-desc {
      display: flex;
      margin-bottom: 2em;
      padding: 1em;
      transition: $transition-default;
      border-radius: 5px;
      outline: 1px solid $dark-bg-color;
      &:hover {
        // test
        outline: 1px solid $highlight-color;
      }
      .steps-arrow {
        width: 3em;
        h1 {
          width: 2em;
          height: 2em;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50%;
          background-color: $color-grey-1;
          margin: 0 auto;
          color: $dark-bg-color;
          margin-bottom: 1em;
        }
        i {
          color: $highlight-color;
          font-size: 3em;
          height: 100%;
        }
      }
      .desc {
        text-align: left;
        margin-left: 1.5em;
        padding: 0.5em 1em 1em 1em;
        h1 {
          color: $highlight-color;
        }
      }
    }
  }
}

.sec5 {
  display: none;
  transform: translateY(0) scale(1);
  background-color: $light-bg-color;
}

.active {
  display: block;
  animation: scaleAnim 1s ease-in-out;
  @keyframes scaleAnim {
    0% {
      transform: translateY(-100%) scaleY(0);
    }
    100% {
      transform: translateY(0) scaleY(1);
    }
  }
}

.header-content {
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-items: center;
  background-color: $dark-bg-color;
  .left-header {
    position: relative;
    height: 100%;
    width: 100%;
    align-items: center;
    display: flex;
    .h-shape {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      background-color: $highlight-color;
      height: 100%;
      width: 65%;
      clip-path: polygon(0 0, 46% 0, 79% 100%, 0% 100%);
    }
    .image {
      position: relative;
      height: 100vh;
      width: 100%;
      z-index: 5;
      display: flex;
      justify-content: center;
      overflow: hidden;
      margin-left: 3em;
      overflow: hidden;
      align-items: center;
      .imageEl {
        height: 80%;
        width: 75%;
        object-fit: cover;
        filter: grayscale(1);
        transition: $transition-default;
        box-shadow: $box-shadow;
        // Hover test
        &:hover {
          filter: grayscale(0);
          box-shadow: $box-shadow;
        }
        @media screen and (max-width: 650px) {
          & {
            opacity: 0;
          }
        }
      }
    }
  }
  .right-header {
    width: 100%;
    min-width: 37em;
    padding-right: 13em;
    padding-left: 2em;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    .services {
      width: 100%;
      display: flex;
      align-items: center;
    }
    img {
      height: 10em;
      width: 10em;
    }
    h1 {
      font-size: 4rem;
      font-weight: 700;
      display: block;
    }
    h2 {
      font-size: 2.5rem;
      font-weight: 400;
    }
    .txt {
      border-right: 0.1em solid $highlight-color;
    }
    span {
      color: $highlight-color;
    }
    .sub-title-highlighted-text {
      font-weight: bolder;
    }
    p {
      margin-top: 2em;
      font-size: 1.3rem;
      font-weight: 400;
    }
    .cta {
      margin-top: 2.8em;
      width: 15em;
      border: 1px solid $highlight-color;
      border-radius: 50px;
      transition: $transition-default;
      cursor: pointer;
      .cta-btn {
        display: flex;
        justify-content: space-between;
        font-size: larger;
        text-decoration: none;
      }
      .cta-txt {
        padding: 0.3em 0.3em;
        margin-left: 1em;
        font-weight: bolder;
        color: $highlight-color;
        transition: $transition-default;
        display: flex;
        justify-content: center;
        align-items: center;
        .arrow {
          opacity: 0;
          color: $secondary-bg-color;
          margin-left: 0em;
          transition: $transition-default;
        }
      }
      .cta-icon {
        font-size: 1.4rem;
        border: 1px solid $highlight-color;
        border-radius: 50%;
        padding: 0.5em 0.6em;
        background-color: $highlight-color;
        color: $secondary-bg-color;
        transition: $transition-default;
        transform: rotate(0deg) scale(1.03);
      }
      //   Hover test
      &:hover {
        background-color: $highlight-color;
        padding-left: 0.2em;
        .cta-txt {
          color: $secondary-bg-color;
        }
        .cta-icon {
          background-color: $secondary-bg-color;
          color: $highlight-color;
          transform: rotate(1080deg) scale(1.03);
        }
        .arrow {
          opacity: 1;
          margin-left: 0.8em;
        }
      }
    }
  }
}

// Trying out responsive font size

html {
  font-size: calc(15px + 0.390625vw);
}

/* Contact section */
#contact {
  $width-box: 30em;
  $offset-x: calc(-10px - 0.390625vw);
  $offset-y: calc(10px + 0.390625vw);

  .contact-container {
    display: grid;
    grid-template-columns: 1fr;
    @media (width > $mq-lg) {
      grid-template-columns: 1fr 1fr;
    }
    .section-title {
      grid-column: 1 / -1;
    }

    .contact-header {
      grid-column: 1 / -1;
      margin: 1.5rem auto;
      font-size: 1.2rem;
      max-width: 40rem;
    }

    .contact-text {
      margin-top: 1.5rem;
      background-color: $color-grey-2;
      color: $light-main-text-color;
      box-shadow: $offset-x $offset-y $highlight-color;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: max(390px, $width-box);
      width: $width-box;
      margin: 1em;
      padding: 1em;
      z-index: 5;
      .triangle {
        position: absolute;
        color: white;
        width: 0;
        height: 0;

        right: 0;
        top: 0;
        bottom: 0;
        translate: 50% 25%;

        border-bottom: calc($width-box / 5) solid $color-grey-2;
        border-top: calc($width-box / 5) solid transparent;
        border-left: calc($width-box / 5) solid transparent;
        border-right: calc($width-box / 5) solid transparent;

        rotate: 45deg;
        z-index: 2;

        filter: drop-shadow(-5px 5px red);
      }
      .dots {
        position: absolute;
        background-image: url(../assets/triangleDots.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        z-index: 3;
        right: calc($width-box / 50);
        bottom: calc($width-box / -50);
        // top: 0;
        width: calc($width-box / 5);
        height: calc($width-box / 5);
        translate: 0 100%;
      }

      // &::after {
      //   content: "";
      //   position: absolute;
      //   inset: 0;
      //   width: 0;
      //   height: 0;
      //   background-color: black;
      //   border-left: 50px solid transparent;
      //   border-right: 50px solid black;
      //   border-bottom: 50px solid transparent;
      // }
    }

    .social-icons {
      margin: 1rem auto;
      // remove when done
      margin-top: 5rem;
      i {
        margin: 1rem 0.5rem;
      }
    }

    .contact-form {
      margin-top: 5rem;
    }
  }
}

