  /* ***Independant Components*** */

  /* Submit Btn */

  #submit-btn {
    border: 1px solid $highlight-color;
    padding: 0.5rem 1.5rem;
    border-radius: 30px;
    width: 40%;
    position: relative;
    left: 60%;
    color: $color-grey-1;
    &:hover {
      cursor: pointer;
      background-color: $highlight-color;
      transition: $transition-default;
    }
    .btn-text:hover {
      margin-left: 0.3rem;
      transition: $transition-default;
    }
    .btn-icon:hover {
      --fa-bounce-start-scale-x: 1;
    }
  }
  .main-btn {
    display: flex;
    justify-content: space-around;
    align-items: center;
    .contact-right {
      margin-left: 0rem;
    }
  }
}

